body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: auto;
  overflow-y: scroll;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/*******************************  Website css Code ********************************/

.home{background-color: #003650;width: 100%;}
.title{width: 100px;height: 48px;}
.menu{margin-left: 120px;}
.menu li{display: inline-block;}
.menu li a{padding-left: 25px;}
.section1{background-image: url('/public/images/backg.jpg');background-size: cover;width: 100%;height: 630px;
background-position: 1200px;background-position: 0px -100px;background-repeat: no-repeat;}
.section1 .box1 .box11 h1{font-size: 60px;font-style: initial;}
.section1 .box1 .box11 h3{font-size: 30px;}
.section1 .box11{position: absolute;left:140px;top: 100px;}
.sportfolio{font-size: 17px;border: 1px solid white;color: white;}
.cindependent{font-weight: 700;font-size: 35px;}
.dcv{background-color: black;padding: 10px 29px;color: white;}
.about .box2{margin-top: 100px;}
.about .box1 img{box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px 0px;}
.aboutme{background-color: rgba(0,0,0,.05);display: inline-block;padding: 7px;text-decoration: none;color: black;font-weight: 600}
.anihead::before{content: 'ok';animation: changetext infinite 8s; width: 200px;font-size: 50px;display: inline-block;width: 390px;}
.anihead{font-size: 40px;}
@keyframes changetext{
  0%{content: 'Web Developer'; }
  30%{opacity: 0.8;}
  46%{content: 'UI/UX DESIGNER';opacity:1;}
  70%{opacity: 0.8;}
  75%{content: 'Content Creator';opacity: 1;}
  90%{opacity: 0.8;}
  100%{content: 'Web Developer';opacity: 1;}
}
.skills{padding: 200px 0;}
.skills .box1{justify-content: space-around;position: relative;}
.skills h3{font-weight: 800;}
.web{display: inline-block;width: 420px;height:4px;background-color:silver;position: absolute
;top: 40px;}
.bi{display: inline-block;width: 420px;height:4px;background-color:silver;position: absolute
  ;top: 90px;}
.logo{display: inline-block;width: 420px;height:4px;background-color:silver;position: absolute
  ;top: 140px;}

.web-inner{display: inline-block;width: 400px;height:4px;background-color:black;position: absolute
  ;top: 0px;z-index: 1;}
.bi-winner{display: inline-block;width: 350px;height:4px;background-color:black;position: absolute
  ;top:0px;z-index: 1;}
.logo-inner{display: inline-block;width:380px;height:4px;background-color:black;position: absolute
  ;top:0px;z-index: 1;}
.deliver{width: 100%;height:638px
;position: relative;}
.deliver .overlay{position: absolute;background-color: rgba(0,0,0,.6);top: 0;left: 0;width: 100%;height: 638px;}
.deliver::before{width: 100%;height:638px;background-image: url('/public/images/back2.jpg');;
  background-repeat: no-repeat;background-size: cover;background-position: 0px -230px;
position: absolute;content: '';top: 0;left: 0;}

.deliver .watch a{margin-top: 130px;font-size: 20px;text-decoration: none;color: white;
border: 3px solid white;padding: 8px 38px; }
.deliver .box1{position: relative;top:86px;z-index: 2;}
.deliver .box1 h4{font-size: 30px;font-weight: 800;}

.bi-google-play::before {
  font-size: 40px;color: black;background-color: white;text-align: center;border-radius:100%;padding: 30px;
 
}
.enquiry{background-image: url('/public/images/back3.jpg');width: 100%;height: 583px;background-size: cover;position: relative;}
.svg1{position: absolute;
  top: -30px;
  left: 0;
  transition: ro;
  transform: rotate(180deg);
  bottom: 200px;z-index: 1;}
  .overlay2{position: absolute;
    background: rgba(0,0,0,.6);
    width: 100%;
    height: 583px;
    opacity: 0.6;}

  .enquiry .box1{display: inline-block;
    position: absolute;
    left: 510px;
    top: 260px;}
  .enquiry h1{font-size: 50px;font-weight: 700;}
    .enqbtn{background-color: white;color: #003650;margin-top: 30px;padding: 12px 20px;}
  .footer{width: 100%;height: 120px;background-color: #000;}
  .footer p{padding-top: 40px;}
  .gallery_product img{width:350px;height: 350px;position: relative;overflow: hidden;transition: 1s;}
  .gbox1{position: relative;width: 350px;margin: auto;overflow: hidden;transition: 1s;}
  .gallery_product .box1{position: absolute;top: 290px;left: 0px;
    color: white;z-index: 2;width:350px;background-color: hsla(0,0%,100%,.6);padding: 20px 0;transform:translateY(60px);
  transition: 0.5s;}
.gbox1:hover .box1{transform: translateY(0px);transition: 0.5s;}
.gallery_product:hover img{transform: scale(1.3);transition: 1s;}
.navbarmain{transition: 0.2s;}
.navbarmain1{width: 1320px;position: fixed;top: 0;left:0;background-color: #000;right: 0;margin: auto;z-index: 2;
transition: 0.8s;padding:8px;border-radius: 10px;}
.navbarmain .bar{width: 64px;
  position: absolute;
  left: 700px;
  top: 30px;
  display:none;
  }
.navbarmain .bar1{width: 28px;height: 3px;background-color: #000;margin: 6px;}
.home{position: relative;}
.slidebar{display: none;width:100%;height: 100vh;background-color:#000;position: absolute;top: 0;left: 0;
z-index: 3;color: white;}
.navsmall ul li{text-decoration: none;list-style: none;padding-top: 19px;}
.close{display: inline-block;margin-left: 240px;}
/************************************************************************************************************************ */

/* Responsive using media query  */

@media(max-width:600px){
  .menu{display: none;}
  .navbarmain{background-color: white;}
  .hbox1{text-align: left;}
  .navbarmain .bar{display: block;top: 23px;left: 310px;}
  .section1{background-position: -400px;}
  .section1 .box11{left: 0px;}
  .about .box1 img{width: 300px;height: 300px;}
  .skills{padding: 70px 0;}
  .l1{width:270px ;}
  .l2{width:270px ;}
  .l3{width:270px ;}
  .s1{width: 264px;}
  .s2{width: 220px;}
  .s3{width: 238px;}
  .deliver::before{background-position:-300px;}
  .enquiry{background-position: -600px;}
  .enquiry .box1{left: 0px;}
  .gbox1:hover .box1{transform: translateY(-20px);}
  .slidebar{display: block;transform: translateX(-400px);transition:0.7s;}
  .navbarmain1{display: none;}
 
}
@media screen and (max-width:800px) and (min-width:500px){
  .slidebar{transform: translateX(-600px);}
  .navbarmain .bar{left: 460px;}
  .section1{background-position: -350px;}
  .section1 .box11{left: 20px;}
  .deliver::before{background-position: -230px;}
  .enquiry{background-position: -500px;}
  .enquiry .box1{left: 38px;}
}